* {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
}
body {
	font-family: 'montseratt', sans-serif;
}
.app {
	background-image: url('./assets/normal-bg.jpg');
	background-size: cover;
	background-position: bottom;
	transition: 2s ease;
}
.app.cold {
	background-image: url('./assets/cold-bg-mob.jpg');
}
.app.warm {
	background-image: url('./assets/warm-bg-mob.jpg');
}
main {
	min-height: 100vh;
	background-image: linear-gradient(
		180deg,
		rgba(0, 0, 0, 0.2),
		rgba(0, 0, 0, 0.75)
	);
	padding: 10px;
}
.search-box {
	width: 100%;
	margin: 0 0 75px;
}
.search-box .search-bar {
	display: block;
	width: 100%;
	padding: 15px;
	-webkit-appearance: none;
	-moz-appearance: none;
	appearance: none;
	background: none;
	border: none;
	outline: none;
	background-color: hsla(0, 0%, 100%, 0.5);
	border-radius: 16px 16px 16px 16px;
	box-shadow: 0 5px rgba(0, 0, 0, 0.2);
	color: #313131;
	font-size: 20px;
	transition: 0.4s ease;
}
.search-box .search-bar:focus {
	background-color: #fff;
}
.location-box .location {
	color: #fff;
	font-size: 32px;
	font-weight: 500;
	text-align: center;
	text-shadow: 3px 3px rgba(50, 50, 70, 0.5);
}
.location-box .date {
	text-align: center;
}
.location-box .date,
.location-box .mTemp {
	color: #fff;
	font-size: 20px;
	font-weight: 300;
	font-style: italic;
	text-shadow: 2px 2px rgba(50, 50, 70, 0.5);
	padding: 5px 0;
}
.location-box .mTemp {
	display: flex;
	align-items: center;
	justify-content: center;
}
.weather-box {
	text-align: center;
}
.weather-box .temp {
	position: relative;
	display: inline-block;
	margin: 30px auto;
	background-color: hsla(0, 0%, 100%, 0.2);
	border-radius: 16px;
	padding: 15px 25px;
	color: #fff;
	font-size: 102px;
	font-weight: 900;
	text-shadow: 3px 6px rgba(50, 50, 70, 0.5);
	text-align: center;
	box-shadow: 3px 6px rgba(0, 0, 0, 0.2);
}
.tempMinMax {
	font-size: 28px;
	font-weight: 700;
}
.tempMinMax,
.weather-box .weather {
	color: #fff;
	text-shadow: 3px 3px rgba(50, 50, 70, 0.5);
}
.weather-box .weather {
	font-size: 38px;
	font-weight: 500;
}
@media (min-device-width: 900px) {
	.app {
		background-image: url('./assets/normal-bg.jpg');
		background-size: cover;
		background-position: 50%;
		transition: 2s ease;
	}
	.app.cold {
		background-image: url('./assets/cold-bg-mob.jpg');
	}
	.app.warm {
		background-image: url('./assets/warm-bg-mob.jpg');
	}
	main {
		min-height: 100vh;
		background-image: linear-gradient(
			0deg,
			hsla(0, 0%, 100%, 0.6),
			rgba(205, 198, 238, 0.75)
		);
		padding: 25px;
	}
}
